  content: "";
  height: 50%;
  width: 0;
  background: #fff;
  transition: all .3s cubic-bezier(.785, .135, .15, .86);
  position: absolute;
}

.button12:before {
  top: 0;
  left: 0;
  right: auto;
}

.button12:after {
  bottom: 0;
  left: auto;
  right: 0;
}

.button12:hover:before {
  width: 100%;
  left: auto;
  right: 0;
}

.button12:hover:after {
  width: 100%;
  left: 0;
  right: auto;
}

.button12:hover span {
  color: #000;
}

.button12:hover em {
  background: #000;
